@charset "UTF-8";
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2em 4em;
  width: 100%;
  height: 80px;
  background-color: #060810;
  backdrop-filter: blur(15px);
  z-index: 12309123;
}
.navbar__content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.navbar__logo {
  flex: 1;
}
.navbar__logo-img {
  padding: 2em 0;
  width: auto;
  height: 100%;
  max-height: 80px;
}
.navbar__icon:hover {
  opacity: 0.6;
}
.navbar__menu {
  display: flex;
  justify-content: center;
  gap: 4em;
  list-style-type: none;
}
.navbar__menu-socials {
  display: none;
}
.navbar__link {
  position: relative;
  color: #94a3b4;
  font-size: clamp(1.4rem, 1.0957rem + 1.7391vw, 2.4rem);
  text-decoration: none;
  transition: color 0.4s;
}
.navbar__link:hover {
  color: #fff;
}
.navbar__link::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  width: 0;
  height: 2px;
  background-color: #fff;
  transform: translateX(-50%);
  transition: width 0.6s 0.2s;
  z-index: -1;
}
.navbar__link.active2 {
  position: relative;
  color: #fff;
  transition: color 0.6s 0.2s;
  z-index: 123;
}
.navbar__link.active2::after {
  width: 100%;
}
.navbar__social {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  gap: 2em;
  list-style-type: none;
}
.navbar__social-link {
  text-decoration: none;
}
.navbar__social-link img {
  width: auto;
  height: 30px;
  transition: opacity 0.3s;
}
.navbar__social-link img:hover {
  opacity: 0.7;
}
.navbar__social-mobile {
  display: none;
  list-style-type: none;
}

/* Dodatkowe style dla responsywności */
@media screen and (max-width: 991px) {
  .navbar__menu,
  .navbar__social {
    display: none;
  }
  .navbar__logo {
    display: block;
  }
  .navbar__icon {
    display: block;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
  }
  .navbar__social-mobile {
    display: flex;
    justify-content: space-evenly;
    gap: 3em;
    margin-top: 5em;
  }
  .navbar__social-mobile .navbar__social-link img {
    height: 40px;
  }
  .navbar__menu {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4em;
    padding-bottom: 10em;
    height: calc(100vh - 80px);
    height: calc(100dvh - 80px);
    background: #060810;
    background-repeat: no-repeat;
    background-image: url("../../dist/img/navbar_neon.svg");
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    opacity: 0;
    transform: translateX(100%);
    transition: transform 0.7s ease-in-out, opacity 0.5s ease-in-out;
    z-index: 1203;
  }
  .navbar__menu-socials {
    display: flex;
  }
  .navbar__menu::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: inherit;
    filter: blur(2px);
    z-index: -1;
  }
}
.line {
  margin: 0.3em;
  width: 3em;
  height: 0.3em;
  background-color: #fff;
  border-radius: 20px;
  transition: background-color 0.3s;
}

.burger {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.burger:focus .line {
  background-color: #9e96e9;
}

@media (min-width: 992px) {
  .burger {
    display: none;
  }
  .navbar__menu {
    display: flex;
  }
}
.no-scroll {
  overflow: hidden;
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 600px;
  height: 100vh;
  height: 100dvh;
  background-image: url("../../dist/img/header_small.jpg");
  background-position: center;
  background-size: cover;
  z-index: 10;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: -1;
}
.header__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2em;
  text-align: center;
}
.header__box-heading {
  color: transparent;
  background: linear-gradient(315deg, rgb(255, 255, 255) 0%, rgb(81, 67, 215) 100%);
  background-clip: text;
  font-size: clamp(2.4rem, 1.75rem + 3.7143vw, 5rem);
  font-weight: 800;
  letter-spacing: 2px;
}
.header__box-text {
  padding: 1em;
  width: 100%;
  font-size: clamp(1.7rem, 1.425rem + 1.5714vw, 2.8rem);
  font-weight: 400;
  text-align: center;
}
.header__box-buttons {
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-top: 4em;
}
.header__box-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.4em;
  padding: 0.5em 1em;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 8px;
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
  transition: 0.1s ease;
  text-decoration: none;
}
.header__box-button:hover {
  color: #a9ffc2;
  border: 2px solid #9e96e9;
  backdrop-filter: blur(15px);
}
.header__box-button:hover svg {
  stroke: #a9ffc2;
}
.header__box-button--first {
  backdrop-filter: blur(15px);
  border: none;
  background: rgb(81, 67, 215);
}
.header__box-button--first:hover {
  border: none;
}
.header__box-button--first:hover svg {
  fill: #a9ffc2;
}

.about {
  margin: 10em 0;
  padding: 6em 0;
}
.about__box {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 2em;
}
.about__box::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -8%;
  width: 200px;
  height: 200px;
  background-image: url("../../dist/img/icons/diamond.svg");
  background-size: cover;
  opacity: 0.1;
  transform: rotate(-15deg);
  z-index: -1;
}
.about__box-img {
  position: relative;
  flex: 1;
}
.about__box-img img {
  width: 100%;
  height: 100%;
  max-height: 400px;
  border-radius: 14px;
  object-fit: cover;
}
.about__box-text {
  flex: 1;
}
.about__box-text p {
  margin-top: 1em;
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
  color: #94a3b4;
}

.gradient-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%; /* Wysokość obszaru z efektem mgły */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 10%, #060810 100%);
}

.technologies {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5em;
  margin-top: 12em;
}
.technologies__heading {
  font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
  font-weight: 500;
  color: #fff;
  text-align: center;
  text-shadow: 0 4px 8px rgba(75, 47, 197, 0.3), 0 6px 20px rgba(9, 70, 238, 0.3);
}
.technologies__box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 6em;
  margin-top: 2em;
  padding: 4em 8em;
  box-shadow: 0 4px 8px rgba(75, 47, 197, 0.1), 0 6px 20px rgba(9, 70, 238, 0.2);
  border-radius: 14px;
  border: 4px solid transparent;
  background: linear-gradient(#060810, #060810) padding-box, linear-gradient(315deg, rgb(255, 255, 255) 0%, rgb(81, 67, 215) 100%) border-box;
}
.technologies__box img {
  width: 60px;
  height: auto;
}

.portfolio {
  position: relative;
}
.portfolio .section__box-heading {
  display: block;
}
.portfolio .gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 20%; /* Wysokość obszaru z efektem mgły */
  background: linear-gradient(to top, rgba(255, 255, 255, 0) 10%, #060810 100%);
}
.portfolio__box {
  position: relative;
  z-index: 10;
}
.portfolio__box::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  height: 80%;
  background-image: url("../../dist/img/portfolio_background.svg");
  background-size: cover;
  opacity: 0.3;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.portfolio__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4em;
  margin-top: 5em;
  width: 100%;
}
.portfolio__project {
  position: relative;
  width: 100%;
  border: 2px solid transparent;
  border-radius: 8px;
  transition: border 0.3s, transform 0.3s, overflow 0.3s ease;
  z-index: 10;
}
.portfolio__project:hover {
  overflow: hidden;
  border: 2px solid #9e96e9;
  transform: scale(0.92);
}
.portfolio__project-image {
  width: 100%;
  height: 100%;
  max-height: 250px;
  object-fit: cover;
}
.portfolio__project-items {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  padding: 2em 4em;
  min-width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 100;
  cursor: pointer;
}
.portfolio__project-items p {
  font-size: clamp(1.8rem, 1.7391rem + 0.3478vw, 2rem);
  color: #fff;
  z-index: 100;
}

.faq {
  position: relative;
  margin-top: 8em;
}
.faq .section__box {
  justify-content: center;
  align-items: center;
  text-align: center;
}
.faq::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 80%;
  margin: 5em 0;
  max-width: 1400px;
  height: 4px;
  background-color: rgba(81, 67, 215, 0.8);
  background-color: #5143d7;
  border-radius: 8px;
  box-shadow: 0px 0px 12px 8px rgba(81, 67, 215, 0.5);
  transform: translateX(-50%);
  z-index: -1;
}
.faq__box {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  overflow: clip;
}
.faq__box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 500px;
  height: 500px;
  background-image: url("../../dist/img/faq_neon.svg");
  transform: rotate(36deg);
  z-index: -1;
}

.accordion {
  margin: 5em auto;
  width: 100%;
  max-width: 800px;
}
.accordion__box {
  margin: 1em 0;
}
.accordion__box .plus {
  font-size: 3rem;
}
.accordion__box-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  width: 100%;
  border: none;
  color: #fff;
  background: linear-gradient(#060810, #060810) padding-box, linear-gradient(315deg, rgb(255, 255, 255) 0%, rgb(81, 67, 215) 100%) border-box;
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
  text-align: left;
  border: 2px solid transparent;
  border-radius: 8px;
  transition: background-color 0.3s;
  cursor: pointer;
}
.accordion__box-btn:hover {
  box-shadow: 0 4px 8px rgba(75, 47, 197, 0.3), 0 6px 20px rgba(9, 70, 238, 0.3);
}
.accordion__info {
  padding: 1em;
  color: #fff;
  font-size: clamp(1.6rem, 1.5rem + 0.5714vw, 2rem);
  transition: opacity 0.3s 0.1s ease-in, max-height 0.6s ease-in-out;
  pointer-events: none;
}
.accordion__info-text {
  max-height: 0;
  opacity: 0;
  transition: max-height 0.8s, opacity 0.4s ease-in;
  pointer-events: none;
}
.accordion .active {
  pointer-events: auto;
}
.accordion .active .accordion__info-text {
  max-height: 250px;
  opacity: 1;
  pointer-events: auto;
}

.question__box {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  gap: 4em;
  margin: 5em 0;
  background-color: #060810;
  overflow: hidden;
  z-index: 1;
}
.question__box p {
  font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
}
.question__box a {
  padding: 0.6em 1.2em;
  color: #fff;
  border-radius: 8px;
  border: 2px solid #fff;
  text-decoration: none;
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
  backdrop-filter: blur(15px);
  transition: color 0.3s, border 0.3s;
}
.question__box a:hover {
  color: hsl(246, 65%, 75%);
  border: 2px solid #9e96e9;
}

.services__cards,
.our-priority__cards {
  position: relative;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  gap: 8em;
  margin-top: 10em;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.services__card,
.our-priority__card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 500px;
  border-radius: 8px;
  text-align: center;
  overflow: hidden;
  z-index: 100;
}
.services__card img,
.our-priority__card img {
  width: auto;
  height: 50px;
}
.services__card-title,
.our-priority__card-title {
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
}
.services__card-description,
.our-priority__card-description {
  color: #94a3b4;
  font-size: clamp(1.6rem, 1.5391rem + 0.3478vw, 1.8rem);
}

.services {
  position: relative;
  overflow-x: clip;
}
.services__box {
  margin: 5em 0;
}
.services .section__box {
  justify-content: center;
  align-items: center;
  padding: 1em;
  text-align: center;
}
.services__card {
  gap: 3em;
  padding: 6em 3em;
  max-height: 350px;
  background-color: #060810;
  border: 2px solid #5143d7;
  box-shadow: 8px 8px 5px 1px rgba(81, 67, 215, 0.4); /* Cień */
}
.services__card-title {
  text-transform: uppercase;
}
.services__card::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 25%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 10%, #060810 100%);
}
.services__card--inactive {
  border: 2px solid rgba(81, 67, 215, 0.3);
  box-shadow: none;
  user-select: none;
}
.services__card--inactive::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(2px);
}
.services__card--inactive::before {
  content: "WKRÓTCE...";
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  font-size: clamp(3.4rem, 3.05rem + 2vw, 4.8rem);
  transform: translate(-50%, -50%);
  z-index: 100;
}

.our-priority {
  margin-top: 10em;
}
.our-priority h3 {
  font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
  text-align: center;
}
.our-priority h3 span {
  color: #5143d7;
}
.our-priority__cards::after {
  content: "";
  position: absolute;
  bottom: -80px;
  left: 50%;
  margin: 0 auto;
  width: 65%;
  height: 4px;
  background: rgb(255, 255, 255);
  background: linear-gradient(315deg, rgb(255, 255, 255) 0%, rgb(81, 67, 215) 100%);
  border-radius: 8px;
  background-size: 200% 100%;
  transform: translateX(-50%);
  animation: gradientAnimation 5s linear infinite;
}
.our-priority__cards::before {
  content: "";
  position: absolute;
  bottom: -10%;
  left: -20%;
  transform: rotate(93deg);
  width: 250px;
  height: 250px;
  background-repeat: no-repeat;
  opacity: 0.4;
  background-size: cover;
  background-image: url("../../dist/img/icons/rocket-launch.png");
  z-index: -1;
}
.our-priority__card {
  gap: 2em;
  padding: 2em 2em 9em;
  height: max-content;
  background-image: linear-gradient(to bottom, #0c0e1d, #0a0d1a, #090b17, #070a14, #060810);
  box-shadow: 0 0 10px 2px #5143d7;
  user-select: none;
}
.our-priority__card::after {
  content: "";
  position: absolute;
  top: 60%;
  left: 60%;
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-image: url("../../dist/img/line-chart.png");
  opacity: 0.2;
  transform: rotate(45deg);
  z-index: -1;
}
.our-priority__card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.our-priority__card img {
  margin: 2em 0;
}
.our-priority__card-description {
  margin: 0.4em 0;
}
.our-priority__card .gradient-overlay {
  height: 40%;
}

@keyframes gradientAnimation {
  0% {
    background-position: 100% 0;
  }
  100% {
    background-position: -100% 0;
  }
}
.contact {
  margin-top: 25em;
}
.contact__email, .contact__icon {
  transition: opacity 0.3s;
}
.contact__email:hover, .contact__icon:hover {
  opacity: 0.6;
}
.contact__email {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: clamp(2rem, 1.5172rem + 2.7586vw, 4rem);
  text-decoration: none;
}
.contact__email-icon {
  width: auto;
  height: 50px;
}
.contact__icon {
  width: auto;
  height: 40px;
}
.contact.section {
  padding: 0;
}
.contact .waves {
  transform: rotate(180deg);
}
.contact__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 8em;
  padding: 4em;
}
.contact__box-socialmedia {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  gap: 6em;
}
.contact .section__box {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em;
  text-align: center;
}
.contact .section__box-heading {
  flex-direction: column;
  gap: 0;
}
.contact .section__box-heading span {
  font-size: clamp(2.1875rem, 1.8103rem + 2.1552vw, 5rem);
  background: rgb(255, 255, 255);
  background: linear-gradient(315deg, rgb(255, 255, 255) 0%, rgb(81, 67, 215) 100%);
  background-clip: text;
  color: transparent;
}
.contact .section__box-text {
  margin-top: 1em;
  width: 90%;
}

.footer {
  height: 60px;
  line-height: 60px;
  background-color: #060810;
  text-align: center;
}
.footer__copyright {
  color: #fff;
  font-size: clamp(1.6rem, 1.5rem + 0.5714vw, 2rem);
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Nunito", sans-serif;
}

body {
  background-color: #060810;
  color: #fff;
  width: 100%;
  overflow-x: hidden;
}

body,
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  font-size: 62.5%;
}

.section {
  opacity: 0;
  transform: translateY(10%);
  transition: opacity 0.5s, transform 0.5s;
}
.section-active {
  opacity: 1;
  transform: translateY(0px);
}

.section {
  padding: 4em;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
}

.waves {
  z-index: -1;
  width: 100%;
  height: auto;
}
.waves img {
  width: 100%;
  height: 80px;
  max-height: 120px;
  opacity: 0.8;
}

.arrow-to-top {
  position: fixed;
  display: block;
  opacity: 0;
  bottom: 20px;
  right: 20px;
  height: 20px;
  width: 20px;
  padding: 1em;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 14px;
  border: 2px solid transparent;
  box-sizing: content-box;
  transform: translateY(150%);
  cursor: pointer;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out, transform 0.3s, border 0.3s ease-in-out, background-color 0.3s ease-in-out;
  z-index: 20130;
}
.arrow-to-top-active {
  transform: translateY(0);
  opacity: 1;
  pointer-events: auto;
}
.arrow-to-top img {
  height: 100%;
  width: auto;
  transform: rotate(180deg);
}
.arrow-to-top:hover {
  border: 2px solid #9e96e9;
  background-color: #9e96e9;
}

.section__box {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.section__box-name {
  color: #5143d7;
  font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
  text-transform: uppercase;
}
.section__box-heading {
  display: flex;
  align-items: center;
  gap: 0.5em;
  color: #fff;
  font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
  font-weight: 800;
}
.section__box-heading img {
  height: 30px;
  width: auto;
}
.section__box-text {
  font-size: clamp(1.6rem, 1.425rem + 1vw, 2.3rem);
  color: #94a3b4;
}

@media (min-width: 576px) {
  .navbar {
    padding: 2em 8em;
  }
  .header__box {
    padding: 2em 4em;
