  }
  .about__box-text h3 img {
    height: 40px;
  }
  .technologies__box {
    flex-direction: row;
  }
  .portfolio__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .services__card {
    padding: 6em;
  }
  .our-priority__card {
    padding: 4em 8em 9em;
  }
  .faq__box {
