  }
  .contact__email-icon {
    height: 80px;
  }
  .contact__icon {
    height: 45px;
    width: auto;
  }
  .section {
    padding: 8em;
  }
  .contact__box {
    padding: 8em 4em;
  }
  .arrow-to-top {
    height: 30px;
    width: 30px;
    bottom: 30px;
    right: 30px;
  }
