    padding: 2em 12em;
  }
  .about__box {
    margin: 0 auto;
  }
  .our-priority__card {
    padding: 4em 4em 9em;
  }
  .our-priority__cards::before {
    height: 400px;
    width: 400px;
  }
  .our-priority__card:nth-child(3),
  .services__card:nth-child(3) {
    grid-column: span 2; /* Dla trzeciego elementu, zajmij dwie kolumny */
  }
  .contact__box {
    flex-direction: row;
  }
  .section {
    padding: 12em;
  }
  .services__cards,
  .our-priority__cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .navbar {
    padding: 2em 6em;
  }
  .navbar__social {
    gap: 4em;
  }
  .navbar__social-link img {
    height: 35px;
  }
  .header {
    background-image: url("../../dist/img/header_big.jpeg");
  }
  .about__box {
    margin: 15em 0;
    max-width: 100%;
    flex-direction: row;
  }
  .about__box-text {
    padding-left: 4em;
  }
  .about__box-img img {
    max-height: none;
  }
  .portfolio .section__box-text {
    width: 60%;
  }
  .section,
  .contact__box {
    padding: 6em;
  }
  .our-priority__card:nth-child(3),
  .services__card:nth-child(3) {
    grid-column: auto; /* Dla trzeciego elementu, zajmij dwie kolumny */
  }
  .our-priority__cards::before {
    left: -22%;
    bottom: -20%;
    transform: rotate(0);
  }
  .portfolio__container,
  .services__cards,
  .our-priority__cards {
    grid-template-columns: repeat(3, 1fr);
  }
  .contact {
    margin-top: 35em;
  }
  .contact__email {
    gap: 0.3em;
  }
  .contact__email-icon {
    height: 100px;
  }
  .contact__icon {
