@use "../base/colors" as *;

.about {
	margin: 10em 0;
	padding: 6em 0;
	&__box {
		position: relative;
		display: flex;
		justify-content: center;
		flex-direction: column;
		gap: 2em;
		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: -8%;
			width: 200px;
			height: 200px;
			// height: 150px;
			// width: 150px;
			// top: -5%;
			// left: -5%;
			background-image: url("../../dist/img/icons/diamond.svg");
			background-size: cover;
			opacity: 0.1;
			transform: rotate(-15deg);
			z-index: -1;
		}
		&-img {
			position: relative;
			flex: 1;
			& img {
				width: 100%;
				height: 100%;
				max-height: 400px;
				border-radius: 14px;
				object-fit: cover;
			}
		}
		&-text {
			flex: 1;
			& p {
				margin-top: 1em;
				font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
				color: #94a3b4;
			}
		}
	}
}
.gradient-overlay {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 20%; /* Wysokość obszaru z efektem mgły */
	background: linear-gradient(
		to bottom,
		rgba(255, 255, 255, 0) 10%,
		#060810 100%
	);
}
.technologies {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	gap: 5em;
	margin-top: 12em;
	&__heading {
		font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
		font-weight: 500;
		color: #fff;
		text-align: center;
		text-shadow: 0 4px 8px rgba(75, 47, 197, 0.3),
			0 6px 20px rgba(9, 70, 238, 0.3);
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 6em;
		margin-top: 2em;
		padding: 4em 8em;
		box-shadow: 0 4px 8px rgba(75, 47, 197, 0.1),
			0 6px 20px rgba(9, 70, 238, 0.2);
		border-radius: 14px;
		border: 4px solid transparent;
		background: linear-gradient(#060810, #060810) padding-box,
			linear-gradient(
					315deg,
					rgba(255, 255, 255, 1) 0%,
					rgba(81, 67, 215, 1) 100%
				)
				border-box;

		& img {
			width: 60px;
			height: auto;
		}
	}
}
