.navbar {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2em 4em;
	width: 100%;
	height: 80px;
	background-color: #060810;
	backdrop-filter: blur(15px);
	z-index: 12309123;
	&__content {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}
	&__logo {
		flex: 1;
		&-img {
			padding: 2em 0;
			width: auto;
			height: 100%;
			max-height: 80px;
		}
	}
	&__icon {
		&:hover {
			opacity: 0.6;
		}
	}
	&__menu {
		display: flex;
		justify-content: center;
		gap: 4em;
		list-style-type: none;
		&-socials {
			display: none;
		}
	}
	&__link {
		position: relative;
		color: #94a3b4;
		font-size: clamp(1.4rem, 1.0957rem + 1.7391vw, 2.4rem);
		text-decoration: none;
		transition: color 0.4s;
		&:hover {
			color: #fff;
		}
		&::after {
			content: "";
			position: absolute;
			bottom: -10px;
			left: 50%;
			width: 0;
			height: 2px;
			background-color: #fff;
			transform: translateX(-50%);
			transition: width 0.6s 0.2s;
			z-index: -1;
		}
		&.active2 {
			position: relative;
			color: #fff;
			transition: color 0.6s 0.2s;
			z-index: 123;
			&::after {
				width: 100%;
			}
		}
	}
	&__social {
		display: flex;
		flex: 1;
		justify-content: flex-end;
		gap: 2em;
		list-style-type: none;
		&-link {
			text-decoration: none;
			& img {
				width: auto;
				height: 30px;
				transition: opacity 0.3s;
				&:hover {
					opacity: 0.7;
				}
			}
		}
		&-mobile {
			display: none;
			list-style-type: none;
		}
	}
}

/* Dodatkowe style dla responsywności */
@media screen and (max-width: 991px) {
	.navbar__menu,
	.navbar__social {
		display: none;
	}

	.navbar__logo {
		display: block;
	}

	.navbar__icon {
		display: block;
		color: #fff;
		font-size: 24px;
		cursor: pointer;
	}
	.navbar__social-mobile {
		display: flex;
		justify-content: space-evenly;
		gap: 3em;
		margin-top: 5em;
		& .navbar__social-link {
			& img {
				height: 40px;
			}
		}
	}
	.navbar__menu {
		position: absolute;
		top: 80px;
		left: 0;
		right: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 4em;
		padding-bottom: 10em;
		height: calc(100vh - 80px);
		height: calc(100dvh - 80px);
		background: #060810;
		background-repeat: no-repeat;
		background-image: url("../../dist/img/navbar_neon.svg");
		color: #fff;
		text-align: center;
		text-transform: uppercase;
		opacity: 0;
		transform: translateX(100%);
		transition: transform 0.7s ease-in-out, opacity 0.5s ease-in-out;
		z-index: 1203;
		&-socials {
			display: flex;
		}
	}

	.navbar__menu::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: inherit;
		filter: blur(2px);
		z-index: -1;
	}
}
.line {
	margin: 0.3em;
	width: 3em;
	height: 0.3em;
	background-color: #fff;
	border-radius: 20px;
	transition: background-color 0.3s;
}
.burger {
	display: flex;
	flex-direction: column;
	padding: 1em;
	background: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	&:focus {
		.line {
			background-color: #9e96e9;
		}
	}
}

@media (min-width: 992px) {
	.burger {
		display: none;
	}
	.navbar__menu {
		display: flex;
	}
}

.no-scroll {
	overflow: hidden;
}
