@use "../base/colors" as *;

.header {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	min-height: 600px;
	height: 100vh;
	height: 100dvh;
	background-image: url("../../dist/img/header_small.jpg");
	background-position: center;
	background-size: cover;
	z-index: 10;
	&::after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.75);
		z-index: -1;
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 2em;
		text-align: center;
		&-heading {
			color: transparent;
			background: linear-gradient(
				315deg,
				rgba(255, 255, 255, 1) 0%,
				rgba(81, 67, 215, 1) 100%
			);
			background-clip: text;
			font-size: clamp(2.4rem, 1.75rem + 3.7143vw, 5rem);
			font-weight: 800;
			letter-spacing: 2px;
		}
		&-text {
			padding: 1em;
			width: 100%;
			font-size: clamp(1.7rem, 1.425rem + 1.5714vw, 2.8rem);
			font-weight: 400;
			text-align: center;
		}
		&-buttons {
			display: flex;
			flex-direction: column;
			gap: 2em;
			margin-top: 4em;
		}
		&-button {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 0.4em;
			padding: 0.5em 1em;
			color: #fff;
			border: 2px solid #fff;
			border-radius: 8px;
			font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
			transition: 0.1s ease;
			text-decoration: none;
			&:hover {
				color: #a9ffc2;
				border: 2px solid $hover-color;
				backdrop-filter: blur(15px);
				& svg {
					stroke: #a9ffc2;
				}
			}

			&--first {
				backdrop-filter: blur(15px);
				border: none;
				&:hover {
					border: none;
					& svg {
						fill: #a9ffc2;
					}
				}
				background: rgb(81, 67, 215);
				// background: linear-gradient(56deg, rgba(81,67,215,1) 0%, rgba(6,8,16,0.8099614845938375) 96%);
			}
		}
	}
}
