@use "../base/colors" as *;
.services__cards,
.our-priority__cards {
	position: relative;
	display: grid;
	grid-template-rows: 1fr;
	grid-template-columns: repeat(1, 1fr);
	place-items: center;
	gap: 8em;
	margin-top: 10em;
	width: 100%;
	height: 100%;
	z-index: 10;
}
.services__card,
.our-priority__card {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	max-width: 500px;
	border-radius: 8px;
	text-align: center;
	overflow: hidden;
	z-index: 100;
	// &:nth-of-type(1) {
	// 	animation: 1s firstAnimation;
	// }
	// &:nth-of-type(2) {
	// 	// animation: ;
	// 	opacity: 0;
	// }
	// &:nth-of-type(3) {
	// 	// animation: ;
	// 	transform: translateX(10%);
	// }
	
	& img {
		width: auto;
		height: 50px;
	}
	&-title {
		font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
	}
	&-description {
		color: #94a3b4;
		font-size: clamp(1.6rem, 1.5391rem + 0.3478vw, 1.8rem);
	}
}

.services {
	position: relative;
	overflow-x: clip;
	&__box {
		margin: 5em 0;
	}
	.section__box {
		justify-content: center;
		align-items: center;
		padding: 1em;
		text-align: center;
	}

	&__card {
		gap: 3em;
		padding: 6em 3em;
		max-height: 350px;
		background-color: #060810;
		border: 2px solid $primary-color;
		box-shadow: 8px 8px 5px 1px rgb(81, 67, 215, 0.4); /* Cień */

		&-title {
			text-transform: uppercase;
		}

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 25%;
			background: linear-gradient(
				to bottom,
				rgba(255, 255, 255, 0) 10%,
				#060810 100%
			);
		}
		&--inactive {
			border: 2px solid rgb(81, 67, 215, 0.3);
			box-shadow: none;
			user-select: none;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgb(0, 0, 0, 0.8);
				backdrop-filter: blur(2px);
			}
			&::before {
				content: "WKRÓTCE...";
				position: absolute;
				top: 50%;
				left: 50%;
				color: #fff;
				font-size: clamp(3.4rem, 3.05rem + 2vw, 4.8rem);
				transform: translate(-50%, -50%);
				z-index: 100;
			}
		}
	}
}
.our-priority {
	margin-top: 10em;
	& h3 {
		font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
		text-align: center;
		& span {
			color: #5143d7;
		}
	}
	&__cards {
		&::after {
			content: "";
			position: absolute;
			bottom: -80px;
			left: 50%;
			margin: 0 auto;
			width: 65%;
			height: 4px;
			background: rgb(255, 255, 255);
			background: linear-gradient(
				315deg,
				rgba(255, 255, 255, 1) 0%,
				rgba(81, 67, 215, 1) 100%
			);
			border-radius: 8px;
			background-size: 200% 100%;
			transform: translateX(-50%);
			animation: gradientAnimation 5s linear infinite;
		}
		&::before {
			content: "";
			position: absolute;
			bottom: -10%;
			left: -20%;
			transform: rotate(93deg);
			width: 250px;
			height: 250px;
			background-repeat: no-repeat;
			opacity: 0.4;
			background-size: cover;
			background-image: url("../../dist/img/icons/rocket-launch.png");
			z-index: -1;
		}
	}
	&__card {
		gap: 2em;
		padding: 2em 2em 9em;
		height: max-content;
		background-image: linear-gradient(
			to bottom,
			#0c0e1d,
			#0a0d1a,
			#090b17,
			#070a14,
			#060810
		);

		// box-shadow: inset 0px 15px 55px -18px #5243d77a;
		box-shadow: 0 0 10px 2px #5143d7;
		user-select: none;
		&::after {
			content: "";
			position: absolute;
			top: 60%;
			left: 60%;
			width: 100%;
			height: 100%;
			background-size: 100%;
			background-image: url("../../dist/img/line-chart.png");
			opacity: 0.2;
			transform: rotate(45deg);
			z-index: -1;
		}
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
			z-index: -1;
		}
		& img {
			margin: 2em 0;
		}
		&-description {
			margin: 0.4em 0;
		}
		& .gradient-overlay {
			height: 40%;
		}
	}
}

@keyframes gradientAnimation {
	0% {
		background-position: 100% 0;
	}
	100% {
		background-position: -100% 0;
	}
}
