@use "../base/colors" as *;

.portfolio {
	position: relative;
	.section__box {
		&-heading {
			display: block;
		}
	}
	.gradient-overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 20%; /* Wysokość obszaru z efektem mgły */
		background: linear-gradient(
			to top,
			rgba(255, 255, 255, 0) 10%,
			#060810 100%
		);
	}
	&__box {
		position: relative;
		z-index: 10;
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			width: 90%;
			height: 80%;
			background-image: url("../../dist/img/portfolio_background.svg");
			background-size: cover;
			opacity: 0.3;
			transform: translate(-50%, -50%);
			z-index: -1;
		}
	}

	&__container {
		display: grid;
		grid-template-columns: 1fr;
		gap: 4em;
		margin-top: 5em;
		width: 100%;
	}
	&__project {
		position: relative;
		width: 100%;
		border: 2px solid transparent;
		border-radius: 8px;
		transition: border 0.3s, transform 0.3s, overflow 0.3s ease;
		z-index: 10;

		&:hover {
			overflow: hidden;
			border: 2px solid $hover-color;
			transform: scale(0.92);
		}
		&-image {
			width: 100%;
			height: 100%;
			max-height: 250px;
			object-fit: cover;
		}
		&-items {
			position: absolute;
			bottom: 0;
			left: 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin: 0 auto;
			padding: 2em 4em;
			min-width: 100%;
			background-color: rgba(0, 0, 0, 0.6);
			z-index: 100;
			cursor: pointer;
			& p {
				font-size: clamp(1.8rem, 1.7391rem + 0.3478vw, 2rem);
				// text-transform: uppercase;
				color: #fff;
				z-index: 100;
			}
		}
	}
}
