.faq {
	position: relative;
	margin-top: 8em;
	.section__box {
		justify-content: center;
		align-items: center;
		text-align: center;
	}
	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 80%;
		margin: 5em 0;
		max-width: 1400px;
		height: 4px;
		background-color: rgba(81, 67, 215, 0.8);
		background-color: #5143d7;
		border-radius: 8px;
		box-shadow: 0px 0px 12px 8px rgba(81, 67, 215, 0.5);
		transform: translateX(-50%);
		z-index: -1;
	}
	&__box {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
		overflow: clip;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 500px;
			height: 500px;
			background-image: url("../../dist/img/faq_neon.svg");
			transform: rotate(36deg);
			z-index: -1;
		}
	}
}

.accordion {
	margin: 5em auto;
	width: 100%;
	max-width: 800px;
	&__box {
		margin: 1em 0;
		.plus {
			font-size: 3rem;
		}
		&-btn {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 1em;
			width: 100%;
			border: none;
			color: #fff;
			background: linear-gradient(#060810, #060810) padding-box,
				linear-gradient(
						315deg,
						rgba(255, 255, 255, 1) 0%,
						rgba(81, 67, 215, 1) 100%
					)
					border-box;
			font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
			text-align: left;
			border: 2px solid transparent;
			border-radius: 8px;
			transition: background-color 0.3s;
			cursor: pointer;
			&:hover {
				box-shadow: 0 4px 8px rgba(75, 47, 197, 0.3),
					0 6px 20px rgba(9, 70, 238, 0.3);
			}
		}
	}
	&__info {
		padding: 1em;
		color: #fff;
		font-size: clamp(1.6rem, 1.5rem + 0.5714vw, 2rem);
		transition: opacity 0.3s 0.1s ease-in, max-height 0.6s ease-in-out;
		pointer-events: none;
		&-text {
			max-height: 0;
			opacity: 0;
			transition: max-height 0.8s, opacity 0.4s ease-in;
			pointer-events: none;
		}
	}
	.active {
		pointer-events: auto;

		.accordion__info-text {
			max-height: 250px;
			opacity: 1;
			pointer-events: auto;
		}
	}
}

.question__box {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	text-align: center;
	gap: 4em;
	margin: 5em 0;
	background-color: #060810;
	overflow: hidden;
	z-index: 1;
	& p {
		font-size: clamp(1.6rem, 1.3rem + 1.7143vw, 2.8rem);
	}
	& a {
		padding: 0.6em 1.2em;
		color: #fff;
		border-radius: 8px;
		border: 2px solid #fff;
		text-decoration: none;
		font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
		backdrop-filter: blur(15px);
		transition: color 0.3s, border 0.3s;
		&:hover {
			color: hsl(246, 65%, 75%);
			border: 2px solid #9e96e9;
		}
	}
}
