@use "layout/navbar" as *;
@use "layout/header" as *;
@use "pages/about" as *;
@use "pages/portfolio" as *;
@use "pages/faq" as *;
@use "pages/services" as *;
@use "pages/contact" as *;
@use "layout/footer" as *;
@use "base/colors" as *;

* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	font-family: "Nunito", sans-serif;
}
body {
	background-color: #060810;
	color: #fff;
	width: 100%;
	overflow-x: hidden;
}

body,
html {
	scroll-behavior: smooth;
	scroll-padding-top: 80px;
	font-size: 62.5%;
}
.section {
	opacity: 0;
	transform: translateY(10%);
	transition: opacity 0.5s, transform 0.5s;
	&-active {
		opacity: 1;
		transform: translateY(0px);
	}
}
.section {
	padding: 4em;
}

.container {
	margin: 0 auto;
	width: 100%;
	max-width: 1400px;
}
.waves {
	z-index: -1;
	width: 100%;
	height: auto;
	& img {
		width: 100%;
		height: 80px;
		max-height: 120px;
		opacity: 0.8;
	}
}

.arrow-to-top {
	position: fixed;
	display: block;
	opacity: 0;
	bottom: 20px;
	// 50
	right: 20px;
	height: 20px;
	width: 20px;
	padding: 1em;
	background-color: rgba(0, 0, 0, 0.5);
	border-radius: 14px;
	border: 2px solid transparent;
	box-sizing: content-box;
	transform: translateY(150%);
	cursor: pointer;
	pointer-events: none;
	transition: opacity 0.3s ease-in-out, transform 0.3s, border 0.3s ease-in-out,
		background-color 0.3s ease-in-out;
	z-index: 20130;
	&-active {
		transform: translateY(0);
		opacity: 1;
		pointer-events: auto;
	}
	& img {
		height: 100%;
		width: auto;
		transform: rotate(180deg);
	}
	&:hover {
		border: 2px solid $hover-color;
		background-color: $hover-color;
	}
}
.section {
	&__box {
		display: flex;
		justify-content: center;
		flex-direction: column;
		&-name {
			color: #5143d7;
			font-size: clamp(1.6rem, 1.4rem + 1.1429vw, 2.4rem);
			text-transform: uppercase;
		}
		&-heading {
			display: flex;
			align-items: center;
			gap: 0.5em;
			color: #fff;
			font-size: clamp(2.4rem, 2.0138rem + 2.2069vw, 4rem);
			font-weight: 800;
			& img {
				height: 30px;
				width: auto;
			}
		}
		&-text {
			font-size: clamp(1.6rem, 1.425rem + 1vw, 2.3rem);
			color: #94a3b4;
		}
	}
}
@media (min-width: 576px) {
	.navbar {
		padding: 2em 8em;
	}
	.header {
		&__box {
			padding: 2em 4em;

			&-buttons {
				flex-direction: row;
			}
		}
	}
	.waves {
		& img {
			height: auto;
		}
	}
	.about {
		&__box {
			&-text {
				& h3 {
					& img {
						height: 40px;
					}
				}
			}
		}
	}
	.technologies {
		&__box {
			flex-direction: row;
		}
	}
	.portfolio {
		&__container {
			grid-template-columns: repeat(2, 1fr);
		}
	}
	.services {
		&__card {
			padding: 6em;
		}
	}
	.our-priority {
		&__card {
			padding: 4em 8em 9em;
		}
	}
	.faq {
		&__box {
			margin-top: 10em;
		}
	}

	.contact {
		&__email {
			&-icon {
				height: 80px;
			}
		}
		&__icon {
			height: 45px;
			width: auto;
		}
	}
	.section {
		padding: 8em;
	}
	.contact__box {
		padding: 8em 4em;
	}
	.arrow-to-top {
		height: 30px;
		width: 30px;
		bottom: 30px;
		right: 30px;
	}
}
@media (min-width: 768px) {
	.navbar {
		padding: 2em 12em;
	}
	.about {
		&__box {
			margin: 0 auto;
		}
	}

	.our-priority {
		&__card {
			padding: 4em 4em 9em;
		}
		&__cards {
			&::before {
				height: 400px;
				width: 400px;
			}
		}
	}
	.our-priority__card,
	.services__card {
		&:nth-child(3) {
			grid-column: span 2; /* Dla trzeciego elementu, zajmij dwie kolumny */
		}
	}
	.contact {
		&__box {
			flex-direction: row;
		}
	}
	.section {
		padding: 12em;
	}
	.services__cards,
	.our-priority__cards {
		grid-template-columns: repeat(2, 1fr);
	}
}
@media (min-width: 992px) {
	.navbar {
		padding: 2em 6em;

		&__social {
			gap: 4em;
			&-link {
				& img {
					height: 35px;
				}
			}
		}
	}
	.header {
		background-image: url("../../dist/img/header_big.jpeg");
	}
	.about {
		&__box {
			margin: 15em 0;
			max-width: 100%;
			flex-direction: row;
			&-text {
				padding-left: 4em;
			}
			&-img {
				& img {
					max-height: none;
				}
			}
		}
	}
	.portfolio {
		.section__box {
			&-text {
				width: 60%;
			}
		}
	}

	.section,
	.contact__box {
		padding: 6em;
	}
	.our-priority__card,
	.services__card {
		&:nth-child(3) {
			grid-column: auto; /* Dla trzeciego elementu, zajmij dwie kolumny */
		}
	}

	.our-priority {
		&__cards {
			&::before {
				left: -22%;
				bottom: -20%;
				transform: rotate(0);
			}
		}
	}
	.portfolio__container,
	.services__cards,
	.our-priority__cards {
		grid-template-columns: repeat(3, 1fr);
	}

	.contact {
		margin-top: 35em;
		&__email {
			gap: 0.3em;
			&-icon {
				height: 100px;
			}
		}
		&__icon {
			height: 50px;
			width: auto;
		}
		&__box {
			max-width: 1200px;
			margin: 8em auto;
		}
		.section__box {
			&-text {
				width: 65%;
			}
		}
	}
	.arrow-to-top {
		height: 40px;
		width: 40px;
		bottom: 40px;
		right: 40px;
	}
}

@media (min-width: 1200px) {
	.header {
		&__box {
			margin-bottom: 20em;
			&-text {
				width: 60%;
			}
		}
	}
	.about {
		&__box {
			&-text {
				& h3 {
					& img {
						height: 50px;
					}
				}
			}
		}
	}
	.faq {
		&::before {
			width: 100%;
		}
	}
	.contact {
		.section__box {
			&-text {
				width: 50%;
			}
		}
	}
}
@media (min-width: 1400px) {
}
.burger-active {
	opacity: 1;
	transform: translateX(0);
}
