.contact {
	margin-top: 25em;
	&__email,
	&__icon {
		transition: opacity 0.3s;
		&:hover {
			opacity: 0.6;
		}
	}
	&__email {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
		font-size: clamp(2rem, 1.5172rem + 2.7586vw, 4rem);
		text-decoration: none;
		&-icon {
			width: auto;
			height: 50px;
		}
	}
	&__icon {
		width: auto;
		height: 40px;
	}
	&.section {
		padding: 0;
	}
	.waves {
		transform: rotate(180deg);
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		gap: 8em;
		padding: 4em;

		&-socialmedia {
			display: flex;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			gap: 6em;
		}
	}
	.section__box {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 1em;
		text-align: center;
		&-heading {
			flex-direction: column;
			gap: 0;
			& span {
				font-size: clamp(2.1875rem, 1.8103rem + 2.1552vw, 5rem);
				background: rgb(255, 255, 255);
				background: linear-gradient(
					315deg,
					rgba(255, 255, 255, 1) 0%,
					rgba(81, 67, 215, 1) 100%
				);
				background-clip: text;
				color: transparent;
			}
		}
		&-text {
			margin-top: 1em;
			width: 90%;
		}
	}
}
